import { Card } from "./module/card.js";
import { Point } from "./module/point.js";
import { ScrollAnimate } from "./module/scrollanimate.js";

class App {
    constructor() {
        this.vw = window.innerWidth;
        this.vh = window.innerHeight;

        this.card = new Card('.intro_card');

        this.mousePos = new Point();
        this.mouseCenter = new Point();

        this.pixelRatio = window.devicePixelRatio > 1 ? 2 : 1;
        
        document.documentElement.style.setProperty('--vh', `${this.vh / 100}px`);
        ScrollAnimate.init();
        
        this.resize();
        window.addEventListener('resize', this.resize.bind(this), false);
        window.addEventListener('pointermove', this.onMove.bind(this), false);
    }

    resize() {
        this.vw = window.innerWidth;
        this.vh = window.innerHeight;
    }

    onMove(e) {
        this.mousePos.x = e.clientX;
        this.mousePos.y = e.clientY;
        this.mouseCenter.x = Math.round(this.mousePos.x - this.vw / 2);
        this.mouseCenter.y = Math.round(this.vh / 2 - this.mousePos.y);

        this.card.mouseMove(this.mouseCenter);
    }

}

window.onload = () => {
    new App();
}